* {
  font-family: Roboto, sans-serif;
}

html, body {
  overflow: hidden;
}

.leftSide {
  left: 1.5%
}

.titles {
  display: flex;
  flex-direction: column;
}

.title-1 {
  position: relative;
  left: 7.5%;
  margin-top: 200px;
  font-style: normal;
  font-weight: 300;
  font-size: 36px;
  letter-spacing: 0.375em;
  color: rgb(199, 199, 199);

  width: fit-content;
  height: fit-content;
  transition: 0.2s all;
}

.title-2 {
  margin-top: -5px;
  left: 7.2%;
  font-weight: 500;
  font-size: 70px;
  letter-spacing: 0.01em;
  
}

.title-1:hover {
  color: rgb(216, 216, 216);
}

.title-2:hover {
  color: rgb(216, 216, 216);
}

.characterGrid {
  position: relative;

  display: grid;
  grid-template-columns: auto auto;
  grid-row-gap: 10px;
  grid-column-gap: 5px;

  margin-top: 25px;
  left: 7.5%;
  width: fit-content
}

.characterItem {
  border: 1px solid white;
  border-radius: 2px;

  display: flex;
  flex-direction: column;

  width: 200px;
  height: 50px;
  color: white;

  box-sizing: border-box;

  background: rgba(196, 196, 196, 0.5);
  transition: 0.2s all;
}

.active {
  border: 1px solid rgba(184, 74, 74, 0.8);
}

.characterItem:hover {
  background: rgba(175, 175, 175, 0.5);
  border: 1px solid rgb(32, 145, 219);
}

.characterId {
  font-weight: 300;
  font-size: 16px;

  margin-top: 2.5%;
  margin-left: 7.5%;
}

.characterName {
  font-weight: 500;
  font-size: 16px;
  margin-left: 8%;
}

.uiButtons {
  position: relative;
  display: flex;
  flex-direction: row;

  margin-top: 40px;;
  left: 7.5%;
  width: fit-content;
}

.playButton {
  width: 150px;
  height: 45px;

  background: linear-gradient(180deg, rgba(82, 164, 196, 0.8) 0%, rgba(82, 164, 196, 0.55) 100%);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 16px;

  color: white;
  font-weight: 500;
  font-size: 22px;
  
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  transition: 0.2s all;
}

.playButton:hover {
  background: linear-gradient(180deg, rgba(72, 151, 182, 0.8) 0%, rgba(71, 147, 177, 0.55) 100%);
}

.deleteButton {
  width: 150px;
  height: 45px;

  background: linear-gradient(180deg, rgba(196, 82, 82, 0.8) 0%, rgba(196, 82, 82, 0.8) 100%);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 16px;

  color: white;
  font-weight: 500;
  font-size: 22px;
  
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  margin-left: 5%;
  transition: 0.2s all;
}

.deleteButton:hover {
  background: linear-gradient(180deg, rgba(185, 74, 74, 0.8) 0%, rgba(184, 74, 74, 0.8) 100%);
}

.uiLinks {
  position: relative;
  display: flex;
  flex-direction: column;

  
  margin-top: 42.5px;
  left: 7.5%;
  width: fit-content;
}

.uiLink {
  width: 400px;
  height: 40px;
  background: rgba(32, 145, 219, 0.2);
  border-radius: 4px;

  margin-bottom: 1.5%;

  font-weight: 500;
  font-size: 16px;

  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;

  color: white;
  transition: 0.2s all;
}

.uiLink:hover {
  background: rgba(32, 145, 219, 0.4);
}

.characterInfo {
  width: 350px;
  height: 140px;
  background: rgba(196, 196, 196, 0.5);
  border-radius: 8px;
  position: fixed;
  top: 175px;
  right: 3.5%;

  display: flex;
  flex-direction: column;

  color: white;
}

.characterInfo-title {
  font-weight: 500;
  font-size: 16px;

  margin-left: 15px;
  margin-top: 10px;
}

.line {
  width: 99%;
  border-top: 1px solid #959595;
  margin-top: 10px;
}

.displayFalse {
  opacity: 0;
  pointer-events:none;
}

.displayTrue {
  transition: opacity 0.5s;
  opacity: 1;
}

.characterInfo-info {
  font-size: 14px;
  margin-left: 15px;
  
}

.characterInfo-bold {
  font-weight: bold;
}

.customDatePickerWidth > .MuiFormControl-root {
   width: 100%;
}

.CUI {
  position: relative;
  margin-top: 200px;
}

.main {
  transition: 0.3s all;
}